import React from 'react'

function ContactUs() {
    return (
        <>
            <section className="contact-section spad">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-4">
                            <div className="contact-text">
                                <h2>Contact Info</h2>
                                <p>
                                    We'd love to hear from you! Whether you have questions about booking your next stay, want to inquire about our amenities and services, or simply wish to share feedback about your experience, we're here to assist you.
                                    Please feel free to reach out to us using the contact form below, and our friendly team will get back to you as soon as possible. Thank you for considering Rustic Villa Hotel for your upcoming getaway.
                                    We look forward to welcoming you soon!
                                </p>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td className="c-o">Address:</td>
                                            <td>Rustic Villa, Kasauli,Himachal Pradesh,India</td>
                                        </tr>
                                        <tr>
                                            <td className="c-o">Phone:</td>
                                            <td>(+91) 99153 12121</td>
                                        </tr>
                                        <tr>
                                            <td className="c-o">Email:</td>
                                            <td>Rusticvillakasauligaon@gmail.com</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <div className="col-lg-7 offset-lg-1">
                            <form action="#" className="contact-form">
                                <div className="row">
                                    <div className="col-lg-6">
                                        <input type="text" placeholder="Your Name" />
                                    </div>
                                    <div className="col-lg-6">
                                        <input type="email" placeholder="Your Email" />
                                    </div>
                                    <div className="col-lg-12">
                                        <textarea placeholder="Your Message"></textarea>
                                        <button type="submit">Submit Now</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="map">
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3423.168026959478!2d76.96609137542583!3d30.909927174499238!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x390f88fc95162b35%3A0x8dc5bdbf86595392!2sRustic%20Villa!5e0!3m2!1sen!2sin!4v1713626014260!5m2!1sen!2sin" 
                       title='Rustic Villa' width="600" height="470" style={{ border: 0 }} allowFullScreen="" loading="lazy" referrerPolicy="no-referrer-when-downgrade"></iframe>
                    </div>
                </div>
            </section>

        </>
    )
}

export default ContactUs